import React, { useState } from "react";
import { BASE_URL } from "../constants";

const Contact = (props) => {
  const [isLoading, setIsLoading] = useState(false);
  const defaultContactForm = {
    contactName: "",
    contactEmail: "",
    contactSubject: "",
    contactMessage: "",
  };
  const [contactForm, setContactForm] = useState(defaultContactForm);
  const [contactFormMsg, setContactFormMsg] = useState({
    err: "",
    submitted: false,
  });

  const handleChange = (e) => {
    setContactFormMsg({
      err: "",
      submitted: false,
    });
    setContactForm({
      ...contactForm,
      [e.target.name]: e.target.value,
    });
  };

  const submitContactMessage = async () => {
    const isNotEmpty = Object.keys(contactForm).reduce(
      (notEmpty, key) => notEmpty && contactForm[key].trim().length > 0,
      true
    );
    if (isNotEmpty) {
      setIsLoading(true);
      try {
        const { status } = await fetch(`${BASE_URL}/admin/message`, {
          method: "POST",
          cache: "no-cache",
          headers: {
            "Content-Type": "application/json",
          },
          body: JSON.stringify({
            sender: contactForm.contactName,
            subject: contactForm.contactSubject,
            email: contactForm.contactEmail,
            content: contactForm.contactMessage,
          }),
        });
        if (status === 200) {
          setContactForm(defaultContactForm);
          setContactFormMsg({ err: "", submitted: true });
        } else {
          setContactFormMsg({
            err: "Oh! There is an error. Feel free to contact me on +91-8546830656.",
            submitted: false,
          });
        }
      } catch (e) {
        console.error("Error:", e)
        setContactFormMsg({
          err: "Oh! There is an error. Feel free to contact me on +91-8546830656.",
          submitted: false,
        });
      }
      setIsLoading(false);
    } else {
      setContactFormMsg({
        err: "Kindly fill all the fields to submit the message.",
        submitted: false,
      });
    }
  };

  if (props.data) {
    var name = props.data.name;
    var street = props.data.address.street;
    var city = props.data.address.city;
    var state = props.data.address.state;
    var zip = props.data.address.zip;
    var phone = props.data.phone;
    var message = props.data.contactmessage;
  }

  return (
    <section id="contact">
      <div className="row section-head">
        <div className="two columns header-col">
          <h1>
            <span>Get In Touch.</span>
          </h1>
        </div>

        <div className="ten columns">
          <p className="lead">{message}</p>
        </div>
      </div>

      <div className="row">
        <div className="eight columns">
          <fieldset>
            <div>
              <label htmlFor="contactName">
                Name <span className="required">*</span>
              </label>
              <input
                type="text"
                value={contactForm.contactName}
                size="35"
                id="contactName"
                name="contactName"
                onChange={handleChange}
              />
            </div>

            <div>
              <label htmlFor="contactEmail">
                Email <span className="required">*</span>
              </label>
              <input
                type="text"
                value={contactForm.contactEmail}
                size="35"
                id="contactEmail"
                name="contactEmail"
                onChange={handleChange}
              />
            </div>

            <div>
              <label htmlFor="contactSubject">
                Subject <span className="required">*</span>
              </label>
              <input
                type="text"
                value={contactForm.contactSubject}
                size="35"
                id="contactSubject"
                name="contactSubject"
                onChange={handleChange}
              />
            </div>

            <div>
              <label htmlFor="contactMessage">
                Message <span className="required">*</span>
              </label>
              <textarea
                cols="50"
                rows="15"
                id="contactMessage"
                name="contactMessage"
                value={contactForm.contactMessage}
                onChange={handleChange}
              />
            </div>

            <div>
              {!isLoading ? (
                <div>
                  <button className="submit" onClick={submitContactMessage}>
                    Submit
                  </button>
                </div>
              ) : (
                <span>
                  <img alt="" src="images/loader.gif" />
                </span>
              )}
            </div>
          </fieldset>

          {contactFormMsg.err && (
            <div id="message-warning"> {contactFormMsg.err}</div>
          )}
          {contactFormMsg.submitted && (
            <div id="message-success">
              <i className="fa fa-check"></i>Your message has been sent, thank you!
              <br />
            </div>
          )}
        </div>

        <aside className="four columns footer-widgets">
          <div className="widget widget_contact">
            <h4>Address and Phone</h4>
            <p className="address">
              {name}
              <br />
              {street} <br />
              {city}, {state} {zip}
              <br />
              <span>{phone}</span>
            </p>
          </div>

          {/* <div className="widget widget_tweets">
                  <h4 className="widget-title">Latest Tweets</h4>
                  <ul id="twitter">
                     <li>
                        <span>
                        This is Photoshop's version  of Lorem Ipsum. Proin gravida nibh vel velit auctor aliquet.
                        Aenean sollicitudin, lorem quis bibendum auctor, nisi elit consequat ipsum
                        <a href="#">http://t.co/CGIrdxIlI3</a>
                        </span>
                        <b><a href="#">2 Days Ago</a></b>
                     </li>
                     <li>
                        <span>
                        Sed ut perspiciatis unde omnis iste natus error sit voluptatem accusantium doloremque laudantium, totam rem aperiam,
                        eaque ipsa quae ab illo inventore veritatis et quasi
                        <a href="#">http://t.co/CGIrdxIlI3</a>
                        </span>
                        <b><a href="#">3 Days Ago</a></b>
                     </li>
                  </ul>
		         </div> */}
        </aside>
      </div>
    </section>
  );
};

export default Contact;



