import React, { useState, useEffect } from "react";

const Messages = () => {
  const [messages, setMessages] = useState([]);
  const [isLoading, setIsLoading] = useState(false);

  const fetchMessages = async () => {
    setIsLoading(true);
    try {
      const response = await fetch(`/admin/message`, {
        method: "GET",
        cache: "no-cache",
        credentials: "same-origin",
        headers: {
          "Content-Type": "application/json",
        },
      });
      setMessages(await response.json());
      setIsLoading(false);
    } catch (e) {
      console.log(e);
      setIsLoading(false);
    }
  };

  useEffect(() => {
    fetchMessages();
  }, []);

  const deleteMessage = async (e, id) => {
    e.preventDefault();
    setIsLoading(true);
    try {
      const { status } = await fetch(`/admin/message/${id}`, {
        method: "DELETE",
        cache: "no-cache",
        credentials: "same-origin",
        headers: {
          "Content-Type": "application/json",
        },
      });
      if (status === 200) {
        fetchMessages();
      } else {
        setIsLoading(false);
      }
    } catch (e) {
      console.log(e);
      setIsLoading(false);
    }
  };

  const refreshMessages = (e) => {
    e.preventDefault();
    fetchMessages();
  };

  return (
    <div className="row">
      <div className="text-right">
        <a href="/" role="button" onClick={(e) => refreshMessages(e)}>
          Refresh
        </a>
      </div>
      <h1>Messages from Visitors</h1>
      {!isLoading ? (
        <ol>
          {messages.length > 0
            ? messages.map((message) => (
                <li key={message._id}>
                  <div>
                    <b>From: </b> {message.sender}
                  </div>
                  <div>
                    <b>Email: </b> {message.email}
                  </div>
                  <div>
                    <b>Message: </b>
                    <pre>
                      <p>| {message.subject} | </p>
                      <p>{message.content}</p>
                    </pre>
                  </div>
                  <a
                    href="/"
                    role="button"
                    onClick={(e) => deleteMessage(e, message._id)}
                  >
                    Delete
                  </a>
                  <hr />
                </li>
              ))
            : "No messages"}
        </ol>
      ) : (
        <span>
          <img alt="" src="images/loader.gif" />
        </span>
      )}
    </div>
  );
};

export default Messages;



